<template>
  <div class="container">
    
    <br>
    <br>
    <div>
      <p class="text-sm font-semibold">Table : {{$route.query.table}}</p>
      <hr>
      <div class="table table-responsive">
        <table>
          <tr>
            <th v-for="(item, index) in field" v-show="item!='id'" :key="index+'field'">{{item}}</th>
          </tr>
          <tr v-for="(item, index) in database" :key="index+'database'">
            <td v-for="(item2, index2) in field" v-show="item2!='id'" :key="index2+'text2'">
              <span @click="removeData(item)">
                {{item[item2]}}
              </span>
            </td>
          </tr>
        </table>
      </div>
    </div>
    <hr>
  </div>
</template>

<script>
  export default {
    data() {
        return {
          database:[],
          field:[],
       
          item:{},
          vdata:{}
        };
    },
    methods: {
       async getData(){
          let that=this;
          const colRef = firebase.collection(firebase.firestore, that.$route.query.table);
          const docsSnap = await firebase.getDocs(colRef);
          let arr=[]
          docsSnap.forEach(doc => {
            let obj={...doc.data(),id:doc.id}
            arr.push(obj)
          })
          this.field = Object.keys(arr[0]);
          that.database=arr
      },
      async removeData(item){
        // let that=this;
        //  let doc=firebase.doc(firebase.firestore, that.$route.table, item.id)
        //  await firebase.deleteDoc(doc)
      }
    },
    mounted() {
      this.getData()
    },
  };
</script>

